import React from "react";
import SidebarSingleItem from "./sidebarSingleItem.jsx";
import SidebarMultiItem from "./sidebarMultiItem.jsx";

const SideBar = () => {
  return (
    <div
      className="col-9 col-sm-5 col-md-3 col-lg-2 bg-white p-0 collapse show d-lg-block sticky-top"
      id="sidebar"
    >
      <small className="mr-2 text-muted"> </small>
      <ul className="nav flex-column p-0 mt-2 mb-3">
        <SidebarSingleItem
          exact={true}
          to="/"
          label=""
          icon="fas fa-desktop pl-1"
        />
        <SidebarMultiItem
          label=" "
          icon="fas fa-users-cog pl-1"
          targetId="userManagement"
          items={[
            { label: "  ", to: "/register" },
            { label: " ", to: "/users" },
            { label: " ", to: "/edit-user/0" },
            { label: " ", to: "/users", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label=" "
          icon="fas fa-rss pl-1"
          targetId="newsManagement"
          items={[
            { label: "  ", to: "/add-news" },
            { label: " ", to: "/news" },
            { label: " ", to: "/edit-news/0" },
            { label: " ", to: "/news", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label="  "
          icon="far fa-question-circle pl-1"
          targetId="tutorialManagement"
          items={[
            { label: "  ", to: "/add-tutorial" },
            { label: "  ", to: "/tutorials" },
            { label: " ", to: "/edit-tutorial/0" },
            { label: " ", to: "/tutorials", navlink: true },
          ]}
        />
      </ul>
    </div>
  );
};

export default SideBar;
